.dashboardTab .inactiveTab {
  background-color: #ccc !important;
  color: #000 !important;
}

.dashboardTab .activeTabHeader{
    background-color: #1A1751 !important;
    color: #fff !important; 
}

.dashboardTab .nav-item{
    margin-left: 3px;
    cursor: pointer;
    min-width: 150px;
    text-align: center;
}

.dashboardTab .nav-item:first-of-type{
    margin-left: 0px;
}

.dashboardTab .nav-link{
    border: none !important;
    border-radius: 0px !important;
    margin-bottom: 0px !important;
}

.dashboardTab .menuButton{
    min-width: 100px !important;
    font-size: 0.85rem !important;
}

.dashboardTab .tabIcon{
    font-size: 0.8rem !important;
}
